/* COMPONENTS */
/* ---------- NAVBAR ---------- */
/* MainNav */
.navbar {
    background-color: #fff !important;
    padding-top: 20px !important;
    padding-bottom: 20px !important;
    box-shadow: 0px 0.5px 10px rgba(000, 000, 000, 0.05);
    border-bottom: 1px solid rgba(000, 000, 000, 0.03);
}
.navbar-brand {
    color: #000 !important;
    font-weight: 700;
}
.navbar a {
    color: #000;
}
.navbar .right-links a {
    margin-left: 15px;
    font-size: 15px;
}
.navbar .right-links a:hover {
    color: #080e2c;
    text-decoration: none;
}
/* SubNav */
.subNav {
    color: #fff;
    border-radius: 0px !important;
}
.subNav a {
    color: #fff;
    margin-right: 20px;
}
.subNav a:hover {
    color: #fff;
    text-decoration: none;
}

/* ---------- BUTTONS ---------- */
/* primaryButton */
.primaryButton {
    padding: 15px 30px;
    background-image: linear-gradient(#104eed, #0037c3);
    color: #fff !important;
    border-radius: 5px;
}
/* secondaryButton */
.secondaryButton {
    padding: 15px 30px;
    background-color: #000;
    color: #fff !important;
    border-radius: 5px;
}
/* whiteButton */
.whiteButton {
    padding: 15px 30px;
    background-color: #fff;
    color: #000 !important;
    border-radius: 5px;
}
/* greenButton */
.greenButton {
    padding: 15px 30px;
    background-image: linear-gradient(#00ff2a, #13d302);
    color: #080e2c !important;
    border-radius: 5px;
